html{
    font-family: sans-serif;
    font-size: 16px;
    box-sizing: border-box;
    color: #242323;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: inherit;
}

#main{
    background-color: #1d1c20;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#APISelector-container{
    padding: 50px 30px;
    width: 100%;
    max-width: 600px;
    border: 1px solid #c0bebe;
    border-radius: 15px;
    background-color: #1d1c20;
    margin: 0 2px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
select{
    color: #1d1c20;
    font-size: 1.3rem;
    width: 100%;
    text-align: center;
    background-color: #e2e0e0;
    display: block;
    margin: 15px 0;
}
#submit-btn{
    color: #c2c0c0;
    font-size: 2rem;
    padding: 10px 30px;
    border-radius: 5px;
    background-color: #1b3f1b;
}

/*app component(renamed tp Quizical)*/
#app-container{
    color: #c2c0c0;
    padding-top: 30px;
    width: 100%;
    min-height: 100vh;
    background-color: #1d1c20;
}

#app-container .questions{
    background-color: #1d1c20;
    width: 100%;
    padding: 30px 5px;
    margin: 0 auto;
}

#question-wrapper{
    border: 1px solid #c2c0c0;
    padding: 10px 5px;
    margin: 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}
#question-wrapper:first-child{
    margin-top: 150px;
}
#question-wrapper #difficulty{
    font-size: 1rem;
    text-align: center;
    color: #fff;
    min-width: 50px;
    padding: 3px;
    border: 1px solid;
    position: absolute;
    right: 2px;
    top: 2px;
}
#question-wrapper .unanswered-questions{
    font-size: 1rem;
    text-align: center;
    color: #fff;
    background-color: #471f1f;
    width: 110px;
    padding: 2px;
    border: 1px solid #fff;
    position: absolute;
    left: 2px;
    top: 2px;
}
#question-wrapper .question{
    font-size: 1.4rem;
    color: #c2c0c0;
    text-align: center;
    padding: 30px;
}
#question-wrapper .answers{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items:stretch;
}

.answers p{
    padding: 5px 15px;
    border-radius: 10px;
    border: 1px solid #c0bebe;
    margin: 10px;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
}
.answers .selected-answer{
    color: #1d1c20;
    background-color: #c2c0c0;
}

.loader{
    width: 100%;
    height: 100vh;
    font-size: 3rem;
    font-weight: bold;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


/*controls*/
.controls{
    width: 100%;
    height: 25%;
    border-bottom: 2px solid #c0bebe;
    box-shadow: 0 0 10px #c0bebe;
    margin: 0 auto 0;
    background-color: #1d1c20;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
}
.controls #submit, .controls #reset{
    font-size: 2.1rem;
    color: #fff;
    padding: 7px 14px;
    margin: 3px 0 0;
    border: none;
    border-radius: 7px;
    background-color: #1b3f1b;
    cursor: pointer;
}
.controls #reset{
    background-color: #471f1f;
}
.controls .scoreboard{
    font-size: 1rem;
    border: 1px solid #fff;
    width: fit-content;
    padding: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}
.controls .scoreboard .score{
    margin-right: 10px;
}

#question-wrapper .answers .correct-answers{
    color: #fff;
    background-color: #1b3f1b;
}

@media screen and (min-width: 850px){
    #app-container .questions{
        width: 85%;
    }
    
    #question-wrapper .answers{
        flex-direction: row;
    }

}
